import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { IOptionsPSComplexRecurringSchedulerExposed } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-recurring-scheduler/ps-complex-recurring-scheduler.component.interfaces';
import { IOptionsPsInputEmailExposed } from 'src/app/commonBussinessSRC/psComponents/ps-keyin-input/ps-input-email/ps-input.email.component.interface';
import { IOptionsPsLovStatementFormatExposed } from 'src/app/commonBussinessSRC/psComponents/ps-select-dropdown/ps-dropdown-lov/ps-lov-statement-format/ps-lov-statement-format.component.interfaces';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { ILoginResponse } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-interfaces';
import { PsCommonBusinessSettings } from 'src/app/commonBussinessSRC/psServices/models/ps-commonBusiness.settings';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { IMapKeyValue, IOptionsPsContainerLookupOptionComponentExposed, IOptionsPsContainerPanel, IOptionsPsKeyinTextarea, IOptionsTemplateStepper } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsNavigatorService } from 'src/app/commonSRC/psServices/navigator/ps-navigator.service';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { OmniBasePage } from '../omni-base/omni-base.page';


@Component({
  selector: 'e-statement-request',
  templateUrl: './e-statement-request.page.html',
  styleUrls: ['./e-statement-request.page.scss'],
})
export class EStatementRequestPage extends OmniBasePage implements OnInit {
  public formGroup: FormGroup = new FormGroup({});
  public eStatementRequestVO = {};
  stepperOptions: IOptionsTemplateStepper = {
    stepperName: 'e_statement_request_key',
    isHorizontalStepper: true,
    numberOfSteps: 1,
    // schedulerStepIndex: 0,
    namesofSteps: [
      'e_statement_request_key',
    ],
    group: this.formGroup,
    submitOptions: {
      submitServiceUrl: PsCommonBusinessSettings.serviceUrl.eStatementRequestEndPoint,
      group: this.formGroup,
    },
    requestObject: this.eStatementRequestVO,
  };
  public accountDetailsOptions: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'account_details_key',
    expanded: true
  };
  emailOptions: IOptionsPsInputEmailExposed = {
    labelKey: 'send_to_email_address_key',
    placeHolder: 'send_to_email_address_key',
    fcName: 'email',
    group: this.formGroup
  };
  statementFormatOptions: IOptionsPsLovStatementFormatExposed = {
    group: this.formGroup,
    fcName: 'fileFormat',
  };
  additionalCommentsOptions: IOptionsPsKeyinTextarea = {
    labelKey: 'additional_comments_key',
    placeHolder: 'enter_additional_comments_key',
    rows: '3',
    cols: '10',
    fcName: 'additionalComments',
    group: this.formGroup
  };
  public panelPeriodicityOptions: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'periodicity_details_key',
    expanded: true
  };
  public schedulerOptions: IOptionsPSComplexRecurringSchedulerExposed = {
    fcName: 'psScheduler',
    group: this.formGroup,
    periodaicityInputCountOptions: {
      fcName: 'noOfPayments',
      labelKey: 'no_of_occurences_key',
    },
    requestObject: this.eStatementRequestVO
  };
  hideDates = true;


  userInformation: ILoginResponse;
  lookupDetails: IOptionsPsContainerLookupOptionComponentExposed = {
    singleLookup: true,
    labelKey: 'account_name_key',
    group: this.formGroup

  };
  public labelValuesMap = new Map<string, IMapKeyValue>();
  public headerMap = new Map<string, IMapKeyValue>();
  public requestMap = new Map<string, string>();
  public editRequestMap = new Map<string, string>();
  accountCardItem: any;
  constructor(public commonService: PsCommonService, public logger: LoggerService, private navService: PsNavigatorService) {
    super();
  }


  ngOnInit() {
    super.init();
    this.baseFormGroup = this.formGroup;
    this.commonProv.copyObject(this.eStatementRequestVO, this.navService.getAllParams() ? this.navService.getAllParams().ScreenVO ? this.navService.getAllParams().ScreenVO : this.navService.getAllParams() : {})
    delete this.eStatementRequestVO['key'];
    this.populateAccountsMap();
    delete this.stepperOptions.requestObject.key;
    this.stepperOptions.schedulerStepIndex = 0;
  }

  ionViewDidEnter() {
    super.ionViewDidEnter();
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.schedulerOptions.periodaicityInputCountOptions.fcName], 0);
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.additionalCommentsOptions.fcName], 0);
    if (this.eStatementRequestVO[this.emailOptions.fcName] === undefined) {
      this.userInformation = this.commonService.session.getValueOf(ConstantCommon.USERINFO);
      if (this.userInformation.customerInfoCO) {
        this.eStatementRequestVO[this.emailOptions.fcName] = this.userInformation.customerInfoCO.coreEmail ? this.userInformation.customerInfoCO.coreEmail : '';
      }
    }
  }
  ngAfterViewInit() {
    //  this.stepperOptions.schedulerStepIndex = 0;
  }
  onChangeSchedule(value) {

    let stateDay;
    if (value !== undefined) {
      if (value.priodicityBy === CommonBussinessConstant.SINGLE_FUTURE_DATE || value.priodicityBy === CommonBussinessConstant.RECURRING_WEEKLY) {
        stateDay = value.startDate.getDay() + 1;
        if (value.priodicityBy === CommonBussinessConstant.SINGLE_FUTURE_DATE) {
          this.eStatementRequestVO['submitType'] = CommonBussinessConstant.SINGLE_FUTURE_DATE_SUBMIT_TYPE;
        } else {
          stateDay = value.startDate.getDate();
          this.eStatementRequestVO['submitType'] = CommonBussinessConstant.WEEKLY_SUBMIT_TYPE;
        }
      }
      if (value.priodicityBy === CommonBussinessConstant.RECURRING_MONTHLY) {
        stateDay = value.startDate.getDate();
        this.eStatementRequestVO['submitType'] = CommonBussinessConstant.MONTHLY_SUBMIT_TYPE;
      }
    }
    this.eStatementRequestVO['day'] = stateDay
  }

  private populateAccountsMap() {
    let accountNumberMap: IMapKeyValue = {};
    let accountBalanceMap: IMapKeyValue = {};
    let investMentCapitalMap: IMapKeyValue = {};
    let profitRateMap: IMapKeyValue = {};
    let tenorMap: IMapKeyValue = {};

    let headerTitleMap: IMapKeyValue = {};
    let headerSubtitleMap: IMapKeyValue = {};

    accountNumberMap = {
      key: 'formattedAccount', // will be used in lookup compoent to get the value of account nummer from list of accounts
      value: '--- --- --- ---',
      isEdit: false,
    };

    accountBalanceMap = {
      key: 'balance', // will be used in lookup compoent to get the value of account balance from list of accounts
      value: '0.0',
      isEdit: false,
    };


    investMentCapitalMap = {
      key: 'investmentCapital',
      value: '0',
      isEdit: false
    };

    profitRateMap = {
      key: 'profitRate',
      value: '0',
      isEdit: false
    };

    tenorMap = {
      key: 'tenor',
      value: '0',
      isEdit: false
    };




    headerTitleMap = {
      key: 'title',
      value: 'generalLedgerBriefNameEng',
      isEdit: false,
    };

    headerSubtitleMap = {
      key: 'subTitle',
      value: 'briefName',
      isEdit: true,
      formGroupMap: {
        placeHolder: 'enter_account_nick',
        fcName: 'briefName',
      }
    };

    this.labelValuesMap.set('account_key', accountNumberMap);
    this.headerMap.set('title', headerTitleMap);
    this.headerMap.set('subTitle', headerSubtitleMap);
    this.requestMap.set('nickNameNumber', 'key');
    this.requestMap.set('oldNickName', 'briefName');
    this.editRequestMap.set('newNickName', 'briefName');
    this.editRequestMap.set('type', 'A');

    this.lookupDetails.fcName = 'fromAccount';
    this.lookupDetails.currencyFlag = 'currencyBriefNameEnglish';
    this.lookupDetails.labelsValueMap = this.labelValuesMap;
    this.lookupDetails.headerMap = this.headerMap;
    this.lookupDetails.formGroup = this.stepperOptions.group;
    this.lookupDetails.balanceMapping = this.balanceMapping();
    this.lookupDetails.isEditable = false;
    this.lookupDetails.requestMap = this.requestMap;
    this.lookupDetails.editRequestMap = this.editRequestMap;

    this.lookupDetails.actionDetailsOptions = [];
    this.lookupDetails.statementOptions = {};

    this.accountCardItem = this.eStatementRequestVO;
  }



  private balanceMapping(): any[] {
    const balanceMappingList = [
      {
        key: 'current_balance_key',
        value: 'balance'
      },
      {
        key: 'available_balance_key',
        value: 'availableBalance'
      },
      {
        key: 'currency_iso_key',
        value: 'currencyBriefNameEnglish'
      }
    ];

    return balanceMappingList;
  }
}
